import * as React from "react";
import * as ReactDOM from "react-dom";

import '../style/main.scss';
import Main from "./components/Main";
import ApiService from "./service/ApiService";
import FakeApiService from "./service/FakeApiService";

import * as Raven from 'raven-js'

let env = process.env.ENV;
let release = 'build-id-' + process.env.BUILD_NUMBER;
// 'https://9c355666b7f04b4e8f17e03fbee8d47b@sentry.ijsfontein.nl/14',
Raven.config(
    process.env.SENTRY_DSN,
    {
        environment: env,
        release: release,
    }).install();
// Raven.captureMessage('Hello World from SENTENCE SOCIETY');

if (process.env.NODE_ENV !== 'production') {
    console.log('FYI: we are not in production mode! (' + process.env.NODE_ENV + ')');
}

// API_ENDPOINT is supplied using webpack.DefinePlugin
declare const API_ENDPOINT: string;

const api = new ApiService(API_ENDPOINT);

const fakeApi = new FakeApiService();

ReactDOM.render(
    <Main apiService={api} />,
    document.getElementById("root")
);